<template>
  <v-layout row wrap>
    <br>
    <v-card>
      <v-card-title primary-title>
        List of basic resources
      </v-card-title>
      <v-card-text>
        <v-data-table
          :headers="headers"
          :items="pract"
          class="elevation-1"
        >
          <template v-slot:item="{ item }">
            <tr>
              <td>
                {{item.resource.id}}
              </td>
              <td>
                {{item.resource.resourceType}}
              </td>
              <td>
                {{item.resource.meta.profile[0]}}
              </td>
            </tr>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </v-layout>
</template>

<script>
export default {
  data: () => ({
    counter: 0,
    pract: [],
    headers: [{
      text: "ID",
      value: "id"
    }, {
      text: "Type",
      value: "resourceType"
    }, {
      text: "Profile",
    }]
  }),
  methods: {
    getPractitioners() {
      fetch("/fhir/Basic?_count=10").then(response => {
        response.json().then(data => {
          this.pract = data.entry
        });
      })
    }
  },
  created() {
    this.getPractitioners()
  }
}
</script>